// vee-validate
export default {
  // usernaem
  username: (value) => {
    // value
    // 1. 
    // 2. 6-20
    // true
    if (!value) return ``
    if (!/^[a-zA-Z]\w{5,19}$/.test(value)) return "6-20"
    return true
  },
  password: (value) => {
    if (!value) return ""
    if (!/^\w{6,24}$/.test(value)) return "6-24"
    return true
  },
}
