// vee-validate
let password = ""
export default {
  // usernaem
  username: (value) => {
    // value
    // 1. 
    // 2. 6-20
    // true
    if (!value) return ``
    if (!/^[a-zA-Z]\w{5,19}$/.test(value)) return "6-20"
    return true
  },
  password: (value) => {
    if (!value) return ""
    if (!/^\w{6,24}$/.test(value)) return "6-24"
    password = value
    return true
  },
  password1: (value) => {
    if (value !== password) return ""
    return true
  },
  code: (value) => {
    if (!value) return "6"
    if (!/^\d{6}$/.test(value)) return "6"
    return true
  },
  mobile(value) {
    if (!value) return ""
    if (!/^1[3-9]\d{9}$/.test(value)) return ""
    return true
  },
}
